.custom-cursor {
  position: fixed;
  top: 0;
  left: 0;
  width: 64px; /* Assuming the rocket image is 64px wide */
  height: 64px; /* Assuming the rocket image is 64px tall */
  pointer-events: none;
  transform-origin: 32px 0; /* Set the origin to the tip of the rocket */
  z-index: 4;
}